import React from 'react';
import './NewsCardAll.css'
import LinkTemplate from "../LinkTemplate";

const NewsCardAll = ({item, path}) => {
    return (
        <div className='news-card-all-container'>
            <div className='news-card-all-left'>
                <div className='news-card-all-image'
                     style={{backgroundImage: `url(${item.image})`}}>
                    <div className='news-card-all-inner-text'>
                        1
                    </div>
                </div>
            </div>
            <div className='news-card-all-right'>
                <LinkTemplate path={`${path}/${item.path}`}>
                    <h3 style={{margin: 0, marginTop: '0.5em', marginBottom: '0.5em'}}>
                        {item.title}
                    </h3>
                </LinkTemplate>
                <p className='news-card-all-date'><i className="far fa-calendar-alt"></i>
                    <span>
                    {new Date(item.date_created).toLocaleDateString()}
                    </span>
                </p>
                <p>
                    {item.summary}
                </p>
                <LinkTemplate path={`${path}/${item.path}`}>
                <span className='news-card-all-button'>Đọc tiếp  <i className="fas fa-caret-right"></i>

                </span>
                </LinkTemplate>
            </div>
        </div>
    );
};

export default NewsCardAll;