import React, {useEffect, useState} from 'react';
import './AllNews.css'
import BodyTemplate from "../../template/BodyTemplate";
import RouteTemplate from "../../template/RouteTemplate";
import NewsTemplate from "../NewsTemplate/NewsTemplate";
import {getItems} from "../../../api/api";
import {useHistory, useLocation} from "react-router-dom"
import Paginate from "../../template/Paginate/Paginate";
import NewsCardAll from "../../template/NewComponents/NewsCardAll";
import MetaTag from "../../template/MetaTag/MetaTag";
import {APP_URL, DESC_DEFAULT, LOGO_DEFAULT} from "../../../constants/Constants";

const AllNews = () => {
    const location = useLocation()
    const url_params = new URLSearchParams(location.search)
    const type = location.pathname === '/tin-tuc' ? 0 : location.pathname === '/ho-tro' ? 2 : 1
    const page = url_params.get('page')
    const history = useHistory()
    const [state, setState] = useState({
        isLoading: true,
        list: [],
        page: page ? page : 1,
        item_per_page: 10,
        total: 0,
        total_page: 0,
    })
    useEffect(() => {
        getItems(`/news?page=${state.page}&item_per_page=${state.item_per_page}&type_news=${type}`, {timeout: 20000})
            .then(res => setState({
                ...state,
                isLoading: false,
                list: res.data.list,
                page: res.data.page,
                item_per_page: res.data.item_per_page,
                total: res.data.total,
                total_page: res.data.total_page
            }))
    }, [page, location.pathname])
    const chosePage = (newPage) => {
        window.scrollTo(0, 0)
        const path = type === 0 ? '/tin-tuc' : type === 1 ? '/gioi-thieu' : '/ho-tro'
        history.push(`${path}?page=${newPage}`)
    }
    return (
        <React.Fragment>
            <MetaTag title={type === 0 ? 'Tin tức' : type === 1 ? 'Giới thiệu' : 'Hỗ trợ'}
            description={DESC_DEFAULT} image_url={LOGO_DEFAULT} url={APP_URL}/>
            <BodyTemplate>
                <RouteTemplate data={[
                    {
                        name: 'Trang chủ',
                        path: '/'
                    },
                    {
                        name: type === 0 ? 'Tin tức' : type === 1 ? 'Giới thiệu' : 'Hỗ trợ',
                    }
                ]
                }/>
            </BodyTemplate>
            <NewsTemplate>
                <section>
                    <h2>{type === 0 ? 'Tin tức' : type === 1 ? "Giới thiệu" : "Hỗ Trợ"}</h2>
                    <section>
                        {
                            state.list.map((item, index) => {
                                return <NewsCardAll item={item} key={index} path={location.pathname}/>
                            })
                        }
                    </section>
                    <div style={{marginTop: '2em'}}>
                        <Paginate totalPage={state.total_page} currentPage={state.page} handlePage={chosePage}/>
                    </div>
                </section>
            </NewsTemplate>
        </React.Fragment>
    );
};

export default AllNews;