import React from 'react';
import LinkTemplate from "../LinkTemplate";
import './Paginate.css'

const listPage = (totalPage) => {
    let list_number = []
    for (let i = 0; i < totalPage; i++) {
        list_number.push(i + 1)
    }
    return list_number
}
const Paginate = ({totalPage, currentPage, handlePage}) => {
    const list_number = listPage(totalPage)
    return (
        <div>
            {
                totalPage === 0 ? null :
                    <div className="paginate-container">
                        <div className={Number(currentPage) !== 1 ? 'paginate-element' : 'paginate-element paginate-inactive'}
                             onClick={() => {
                                 handlePage(Number(currentPage) - 1)
                             }}>&laquo;</div>
                        {
                            list_number.map(item => {
                                return <div className='paginate-element' style={{
                                    backgroundColor: item == currentPage ? 'var(--main-bg-color)' : 'white',
                                    color: item == currentPage ? 'white' : 'black',
                                }} key={item} onClick={() => {
                                    handlePage(item)
                                }}>{item}</div>
                            })
                        }
                        <div className={Number(currentPage) !== totalPage ? 'paginate-element' : 'paginate-element paginate-inactive'}
                             onClick={() => {
                                 handlePage(Number(currentPage) + 1)
                             }}>&raquo;</div>
                    </div>
            }
        </div>
    );
};

export default Paginate;