import React from 'react';
import './NewsTemplate.css'
import RouteTemplate from "../../template/RouteTemplate";
import ToolbarMobile from "../../../MainComponent/header/components/ToolbarMobile";
import BodyTemplate from "../../template/BodyTemplate";
import {useSelector} from "react-redux";
import SaleItem from "../../template/SaleItem";
import HotProductNav from "../../template/HotProduct/HotProductNav";

const NewsTemplate = ({children}) => {
    return (
        <BodyTemplate>
            <section className='template-news-container'>
                <NavLeft/>
                <div className='template-news-left'>
                    {children}
                </div>
            </section>
        </BodyTemplate>
    );
};

export default NewsTemplate;

export const NavLeft = () => {
    return <div className='template-news-right'>
        <h3 className='template-new-left-title'>
            Danh mục
        </h3>
        <div className='template-new-left-menu'>
            <ToolbarMobile/>
        </div>
        <div style={{marginTop: '3em'}}>
            <HotProductNav/>
        </div>
    </div>
}